	module.exports = {"data":{"post":{"html":"<p>We have an exchange student from Italy with us this week (his family hosted our son for a week last fall). We were a bit worried about how to keep him occupied, since we're out in the country and far from the school. We needn't have worried. The students (hosts and guests) hang out in Lüneburg after school and they only come home to sleep.</p>\n<p>BTW, German is cool in that it was a different word for grade and high school students <em>(Schüler)</em> than for college students <em>(Studenten)</em>. We're talking about <em>Schüler</em> here. And that my wife and I met when she was an <a href=\"http://www.papascott.de/archives/2009/11/17/and-i-saw-her-standing-there/\">exchange student</a> <em>(Schüler)</em> in Minnesota is totally beside the point!</p>","fields":{"title":"Student Exchange","date":"05 Apr 2016","url":"https://papascott.de/2016-04-05-student-exchange/"}}},"pathContext":{"slug":"/2016-04-05-student-exchange/","prev":{"fields":{"title":"Gliding","date":"05 Apr 2016","slug":"/2016-04-05-gliding/"}},"next":{"fields":{"title":"The Ants","date":"18 Apr 2016","slug":"/2016-04-18-the-ants/"}}}}

